@import url(https://fonts.googleapis.com/css2?family=Lobster&family=PT+Sans+Narrow&family=Ranchers&family=Roboto&display=swap);
#preview {
  height: 60vh;
  padding: 20px;
  background-color: white;

  border-radius: 10px 10px 0 0px;
}
#generator {
  background-color: rgba(255,255,255, .05);
  border-radius: 10px;
  margin-left: 15px;
  color: white;
  padding: 20px;
}

.inputs-section {
  padding: 10px 0;
}

.input-grp {
  display:flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px 0;
}

.input-label {
  text-align: left;
  width:25%;
}


.pickcolors {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.picker {
  width: 20px;
  height: 20px;
  border-radius: 3px;
  background-color: rgba(255,255,255,0.1);
  border: 2px solid rgba(255,255,255,0.2);
  padding: 2px;
  margin: 2px;

  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s;
}

.block-color {
  width:100%;
  height: 100%;
}

.picker:hover {
  cursor: pointer;
}

.picker.active {
  border: solid 2px rgba(255,255,255, 0.6);
  box-shadow: 0 0 1px 1px rgba(255,255,255,0.4);
  transition: all 0.2s;
}

input[type="text"],
input[type="range"],
select {

  padding: 4px 10px;
  border-radius: 3px;
  width: 60%;
  display: flex;
}

input[type="text"]:focus,
select:focus {
  outline: none;
} 
.menu {
  background-color: rgba(255,255,255, .05);
  border-radius: 10px;
  margin-top: 15px;
}

.menu-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

.menu-list-item {
  position: relative;
}

.menu-list .icon {
  width: 100%;
  height: 100%; 
}

.menu .btn {
  display: flex;
  width: 100%;
  height: 100%;
  border: 0;

  cursor: pointer;
  position: absolute;
  top:0;
  left:0;
  
  z-index:1;
}

.menu .btn:focus {

  border: 2px;
  outline: none;
}

.btn-menu {
  background-color: transparent;
}

.block-btn {
  margin: 20px;
  padding: 20px;
  border: solid 2px rgba(255,255,255, 0.2);
  background-color: rgba(255,255,255, .1);
  border-radius: 20px;
  height: 100px;
  width: 100px;

  display:flex;
  justify-content: center;
  align-items: center;

  transition: all 0.2s;
}

.btn-menu.active + .block-btn{
  border: solid 2px rgba(255,255,255, 0.6);
  box-shadow: 0 0 10px 1px rgba(255,255,255, 0.5);
  transition: all 0.2s;
}
.name {
  border-radius: 0 0 10px 10px;
  padding: 20px 0;
  background-color: #fff;
  color: #101;

  font-size: 24px;

  font-weight: 700;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding:0;
  border: 0;
}

.App {
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;


  color: white;
  padding: 20px;
  font-family: "PT Sans", sans-serif;
}

.d-flex {
  display: flex;
}

.d-row {
  flex-direction: row;
}

.d-column {
  flex-direction: column;
}

.box {
  background-color: #161725;
  color: white;
  padding: 20px;
  border-radius:20px;
box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}



